'use client';
import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/components/auth-provider';
import EventForm, { EventFormValues } from '@/components/EventForm';
import { db } from '@/lib/firebase';
import {
  collection,
  addDoc,
  query,
  where,
  onSnapshot,
  orderBy,
  Timestamp,
  deleteDoc,
  doc,
} from 'firebase/firestore';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Skeleton } from '@/components/ui/skeleton';
import { Calendar } from '@/components/ui/calendar';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogClose,
} from '@/components/ui/dialog';

interface EventItem {
  id: string;
  title: string;
  date: string; // ISO
  time: string;
  description: string;
  priority: string;
}

function getInitials(name: string) {
  return name
    .split(' ')
    .map((n) => n[0]?.toUpperCase())
    .join('');
}

export default function Home() {
  const { user, loading } = useAuth();
  const router = useRouter();
  const [events, setEvents] = useState<EventItem[]>([]);
  const [saving, setSaving] = useState(false);
  const [eventsLoading, setEventsLoading] = useState(true);
  const [calendarDate, setCalendarDate] = useState<Date | undefined>(new Date());
  const [formOpen, setFormOpen] = useState(false);
  const [formDate, setFormDate] = useState<Date | undefined>(undefined);

  // Витягуємо ім'я та прізвище з displayName, якщо є
  const displayName = user?.displayName || 'Користувач';
  const initials = getInitials(displayName);

  useEffect(() => {
    if (!loading && !user) {
      router.replace('/auth');
    }
  }, [user, loading, router]);

  useEffect(() => {
    if (!user) return;
    setEventsLoading(true);
    const q = query(
      collection(db, 'events'),
      where('userId', '==', user.uid),
      orderBy('date', 'asc'),
    );
    const unsub = onSnapshot(q, (snap) => {
      setEvents(snap.docs.map((d) => ({ id: d.id, ...d.data() }) as EventItem));
      setEventsLoading(false);
    });
    return () => unsub();
  }, [user]);

  const handleAddEvent = async (values: EventFormValues) => {
    if (!user) return;
    setSaving(true);
    try {
      await addDoc(collection(db, 'events'), {
        userId: user.uid,
        title: values.title,
        date: values.date?.toISOString().slice(0, 10),
        time: values.time,
        description: values.description,
        priority: values.priority,
        createdAt: Timestamp.now(),
      });
      setFormOpen(false);
    } finally {
      setSaving(false);
    }
  };

  const handleDelete = async (id: string) => {
    if (!window.confirm('Видалити подію?')) return;
    await deleteDoc(doc(db, 'events', id));
  };

  if (loading) {
    return (
      <div className="min-h-screen p-8">
        <div className="flex items-center gap-3 mb-8">
          <Skeleton className="h-12 w-12 rounded-full" />
          <div>
            <Skeleton className="h-4 w-32 mb-2" />
            <Skeleton className="h-4 w-24" />
          </div>
        </div>
        <Skeleton className="h-16 w-full max-w-lg mb-8" />
        <Skeleton className="h-40 w-full max-w-lg" />
      </div>
    );
  }

  if (!user) return null;

  return (
    <div className="font-sans min-h-screen p-8 bg-background">
      <div className="flex items-center gap-3 mb-8 fixed top-8 left-8 z-10">
        <Avatar>
          <AvatarImage src={user.photoURL || undefined} alt={user.email || ''} />
          <AvatarFallback>{initials}</AvatarFallback>
        </Avatar>
        <div>
          <div className="font-bold text-lg">Вітаємо, {user.email}!</div>
        </div>
      </div>
      <div className="flex flex-col items-center pt-24 gap-8">
        <div className="w-full max-w-2xl">
          <div className="text-2xl font-bold mb-8 text-center">Ваш персональний кабінет</div>
          <Calendar
            mode="single"
            selected={calendarDate}
            onSelect={(date) => {
              setFormDate(date);
              setFormOpen(true);
            }}
            className="rounded-md border shadow mb-8 w-full"
            captionLayout="dropdown"
          />
        </div>
        <Dialog open={formOpen} onOpenChange={setFormOpen}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Додати подію</DialogTitle>
            </DialogHeader>
            <EventForm onSubmit={handleAddEvent} loading={saving} initial={{ date: formDate }} />
            <DialogClose asChild>
              <button className="mt-4 text-gray-500 hover:text-black">Закрити</button>
            </DialogClose>
          </DialogContent>
        </Dialog>
        <div className="w-full max-w-lg mt-8">
          <h2 className="text-xl font-semibold mb-4">Ваші події</h2>
          {eventsLoading ? (
            <>
              <Skeleton className="h-20 w-full mb-4" />
              <Skeleton className="h-20 w-full mb-4" />
            </>
          ) : events.length === 0 ? (
            <div className="text-gray-500">Подій ще немає</div>
          ) : (
            <ul className="space-y-4">
              {events.map((ev) => (
                <li
                  key={ev.id}
                  className="border rounded p-4 flex flex-col gap-1 bg-white dark:bg-gray-800"
                >
                  <div className="flex justify-between items-center">
                    <span className="font-bold">{ev.title}</span>
                    <span className="text-xs px-2 py-1 rounded bg-gray-200 dark:bg-gray-700">
                      {ev.priority}
                    </span>
                  </div>
                  <div className="text-sm text-gray-600 dark:text-gray-300">
                    {ev.date} {ev.time}
                  </div>
                  {ev.description && <div className="text-sm mt-1">{ev.description}</div>}
                  <button
                    onClick={() => handleDelete(ev.id)}
                    className="text-red-500 text-xs mt-2 self-end"
                  >
                    Видалити
                  </button>
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>
    </div>
  );
}
